.section-form {
  // max-width: 1680px;
  // min-width: 320px;
  // min-height: 507px;
  margin: 0 auto;
  font-family: Montserrat;
  font-style: normal;
  text-align: center;
  color: $white;
  background-color: $text-dark;
  background-image: url('../images/form_img/bg_img_tablet-min.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 82% 50%;
  padding: 50px 0;

  @media screen and (min-device-pixel-ratio: 2) and (min-width: 320px),
    screen and (min-resolution: 192dpi) and (min-width: 320px),
    screen and (min-resolution: 2dppx) and (min-width: 320px) {
    background-image: url('../images/form_img/bg_img_tablet@2x-min.png');
  }
}

.registration-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.registration-form__header {
  @include font(800, 14px, 1.357, 0.03em, $white);
  margin-bottom: 30px;
}

.registration-form__description {
  @include font(600, 12px, 1.25, 0.08em, $white);
  margin-bottom: 30px;
}

.registration-form__header .text__accent,
.registration-form__description .text__accent {
  color: $accent-color;
}

.registration-form__btn {
  width: 260px;
  height: 48px;
  padding: 14px 0;
  margin-top: 50px;
  border: none;
  outline: none;
  @include font(700, 12px, 1.25, 0.08em, $white);
  color: $white;
  text-transform: uppercase;
  text-align: center;
  background-color: $accent-color;
  border: 2px solid $accent-color;

  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: transparent;
    color: $accent-color;
  }
}

.registration-form__input {
  width: 280px;
  height: 48px;
  padding-left: 28px;
  color: $white;
  background-color: transparent;
  border: 1px solid $white;
  @include font(600, 14px, 1.214, 0.08em, $white);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

  outline: none;

  &::placeholder {
    @include font(600, 14px, 1.214, 0.08em, $white);
    color: $white;
  }

  &:hover::placeholder,
  &:focus::placeholder {
    color: $accent-color;
  }

  &:hover,
  &:focus {
    border: 1px solid $accent-color;
  }
}

.registration-form__field + .registration-form__field {
  margin-top: 20px;
}

@media screen and (min-width: $tablet) {
  .registration-form__header {
    @include font(800, 26px, 1.307, 0.03em, $white);
  }

  .registration-form__description {
    @include font(600, 14px, 1.214, 0.08em, $white);
    margin-bottom: 30px;
  }

  .registration-form__input {
    width: 390px;
  }

  .registration-form__btn {
    margin-top: 30px;
  }
}

@media screen and (max-width: $desktop - 1) and (min-width: $tablet) {
  .section-form {
    padding: 50px 0 70px 0;
  }
  .registration-form__header {
    @include font(800, 26px, 1.3, 0.03em, $white);
  }

  .registration-form__description {
    @include font(600, 14px, 1.21, 0.08em, $white);
    margin-bottom: 40px;
  }
}

@media screen and (min-width: $desktop) {
  .section-form {
    padding: 100px 0 62px 0;
    background-color: $text-dark;
    background-image: url('../images/form_img/bg_img_desktop-min.png');
    background-repeat: no-repeat;
    background-position: center;

    @media screen and (min-device-pixel-ratio: 2) and (min-width: $desktop),
      screen and (min-resolution: 192dpi) and (min-width: $desktop),
      screen and (min-resolution: 2dppx) and (min-width: $desktop) {
      background-image: url('../images/form_img/bg_img_desktop@2x-min.png');
    }
  }

  .registration-form__header {
    width: 1090px;
    @include font(800, 46px, 1.3, 0.03em, $white);
    margin-bottom: 50px;
  }

  .registration-form__description {
    @include font(600, 30px, 1.233, 0.08em, $white);
    margin-bottom: 56px;
  }

  .registration-form__input {
    width: 478px;
    height: 70px;
    @include font(600, 20px, 1.2, 0.08em, $white);
  }

  .registration-form__field + .registration-form__field {
    margin-top: 30px;
  }

  .registration-form__btn {
    margin-top: 80px;
    width: 354px;
    height: 70px;
    @include font(700, 14px, 1.214, 0.08em, $white);
  }
}
