$text-dark: #111518;
$text-semi-dark: #3d3d3d;
$text-grey: #b9b9b9;
$accent-color: #ff6b09;
$white: #ffffff;
$bg-gray: #f1f1f1;
$main-font: 'Montserrat', sans-serif;
$transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

$mobile: 320px;
$tablet: 768px;
$desktop: 1344px;

@mixin font($fw, $fs, $lh, $ls, $cl) {
  font-weight: $fw;
  font-size: $fs;
  line-height: $lh;
  letter-spacing: $ls;
  color: $cl;
}
